import React from "react";
import "./Trailer.css";
import LogoTrailer from "../../assets/logo-trailer.png";

export default function Trailer() {
  return (
    <div className="trailer">
      {/* Left */}
      <div className="serie">
        <div className="title">
          <img src={LogoTrailer} alt="logo série" />
        </div>
        <div className="synopsis">
          Atteint d'un cancer en phase terminale, un prof de chimie se lance
          dans la fabrication et la vente de méthamphétamines pour assurer
          l'avenir financier de sa famille.
        </div>
        <div className="buttons">
          <button className="btn btn-play">
            <i className="fas fa-play"></i> <span>Lecture</span>
          </button>
          <button className="btn btn-info">
            <i className="fas fa-info-circle"></i> <span>Plus d'infos</span>
          </button>
        </div>
      </div>
      {/* right */}
      <div className="age-replay">
        <button className="replay">
          <i className="fas fa-redo"></i>
        </button>
        <div className="age">16+</div>
      </div>
    </div>
  );
}
