import React, { useRef, useEffect, useState } from "react";
import "./NavBar.css";
import ImgProfil from "../../assets/img-profil.png";
import NetflixBig from "../../assets/netflix-big.svg";
import Vikings from "../../assets/notifications/vikings.jpg";
import Top10 from "../../assets/notifications/top10.jpg";
import Peaky from "../../assets/notifications/peaky-blinders.jpg";
import Jeunesse from "../../assets/jeunesse.png";

export default function NavBar() {
  const [toggleInput, setToggleInput] = useState(false);
  const [notifState, setNotifState] = useState([
    {
      description: "Top 10 d'aujourd'hui :France",
      name: "regarder tous les titres",
      date: "Il y a un jour",
      img: process.env.PUBLIC_URL + Top10,
    },
    {
      description: "Reprenez la lecture",
      name: "Peaky Blinders",
      date: "Il y a deux jours",
      img: process.env.PUBLIC_URL + Peaky,
    },
    {
      description: "Nouveauté",
      name: "Vikings Saison 6",
      date: "Il y a un mois",
      img: process.env.PUBLIC_URL + Vikings,
    },
  ]);

  const ref = useRef();

  const showInput = () => {
    setToggleInput(!toggleInput);
  };

  useEffect(() => {
    window.addEventListener("click", (e) => {
      if (!e.path[1].classList.contains("form")) {
        setToggleInput(false);
      }
    });
  }, []);

  useEffect(() => {
    if (toggleInput) {
      ref.current.classList.add("active-input");
    } else {
      ref.current.classList.remove("active-input");
    }
  }, [toggleInput]);

  return (
    <nav>
      <div className="categories">
        <div className="logo">
          <img src={NetflixBig} alt="logo-netflix" />
        </div>
        <a href="#!">Accueil</a>
        <a href="#!">Séries</a>
        <a href="#!">Films</a>
        <a href="#!">Nouveautés les plus regardées</a>
        <a href="#!">Ma liste</a>
      </div>
      <div className="options">
        <form className="form" ref={ref}>
          <label className="form" htmlFor="search" onClick={() => showInput()}>
            <i className="fas fa-search"></i>
          </label>
          <input
            className="form"
            type="text"
            id="search"
            placeholder="Titres, personnes, genres"
          />
        </form>

        <a href="#!">DIRECT</a>
        <a href="#!">JEUNESSE</a>
        <div className="notification">
          <i className="fas fa-bell"></i>
          <div className="notif-active">
            <i class="fas fa-sort-up"></i>
            <ul>
              {notifState.map((item, index) => {
                return (
                  <li key={index}>
                    <img src={item.img} alt="logo de la série" />
                    <div className="description">
                      <span>{item.description}</span>
                      <span>{item.name}</span>
                      <span className="time">{item.date}</span>
                    </div>
                  </li>
                );
              })}
            </ul>
          </div>
        </div>
        <div className="profil">
          <img src={process.env.PUBLIC_URL + ImgProfil} alt="photo de profil" />
          <i className="fas fa-sort-down"></i>
          <div className="menu-profil">
            <div className="accounts">
              <a href="#!">
                <img
                  src={process.env.PUBLIC_URL + Jeunesse}
                  alt="image profil"
                />{" "}
                Jeunesse
              </a>
              <a href="#!">Gérer les profils</a>
            </div>
            <div className="settings">
              <a href="#!">Compte</a>
              <a href="#!">Centre d'aide</a>
              <a href="#!">Se déconnecter</a>
            </div>
          </div>
        </div>
      </div>
    </nav>
  );
}
